@charset "utf-8";

// Global styles for the Lanzara Public Site
// These override default settings from the theme we are using, in addition to providing
// various utility classes that are needed for formatting + layout in some places

@import "vendor/tailwind.min.css";

/* Components */

/* Typography */
.carousel-container {
  background: rgba(0, 0, 0, 0.4);
}

.team-member {
  max-width: 32rem;
}

.text-white-alpha-800 {
  color: rgba(255, 255, 255, 0.8)
}

.text-white-alpha-600 {
  color: rgba(255, 255, 255, 0.6)
}

.text-white-alpha-400 {
  color: rgba(255, 255, 255, 0.4)
}

.project-header {
  background: linear-gradient(0.3turn, #333333, #565656);
  //background: linear-gradient(0.3turn, #0077b6, #00b4d8);
}

.landing-splash {
  background: linear-gradient(0.3turn, #333333, #454545);
}

.dropdown.closed {
  .dropdown-content {
    display: none;
  }
}

.portrait {
  width: 180px !important;
  min-width: 180px;
  img {
    min-width: 180px;
    width: 180px;
  }
}

.research-highlights .highlight {
  margin: 2rem 4rem;
}



body.landing #main {
  padding-left: 0;
  padding-right: 0;
}

// Customize media queries
@media (min-width: 80em) {
  .masthead__inner-wrap {
    max-width: 100%;
  }

  #main {
    max-width: 1600px;
  }
}

.sidebar.sticky {
  height: unset;
  max-height: calc(100% - 8em);
  overflow-y: scroll;
}

figure.figure {
  display: inline-block;
  margin: 0.5rem;
}

/* reset font animations to exclude KaTeX, this prevents bouncing during resizing */
b, i, strong, em, blockquote, p, q, span, figure, img, h1, h2, header, input, a, tr, td, form button, input[type="submit"],
.btn, #goog-wm-sb, .highlight, .archive__item-teaser {
  &:not(.katex) {
    -webkit-transition: none;
    transition: none;
  }
}

.highlight-image {
  object-fit: contain;
}

.funding-attribution {
  .logo {
    height: 4rem;
  }

  margin-bottom: 1rem;
}

.carousel {
  .item:not(.active) {
    display: none;
  }

  .item {
    max-height: 350px;
    height: 350px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    video {
      max-height: 350px;
      max-width: 100%;
      width: 100% !important;
      background-size: cover;
      object-fit: cover;
      z-index: -1;
    }
  }
}

.landing-container {
  position: relative;
  height: 350px;
}
.lab-name-callout {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  min-height: 100%;
  height: 100%;
  z-index: 10;
  font-size: 80px;

  h1 {
    margin-top: 30px;
    margin-left: 50px;
  }
}

.landing-carousel-container {
  position: absolute;
  width: 100%;
  min-height: 100%;
  height: 100%;
  top: 0;
}